import React from "react";
import {InnerBlocks as DefaultInnerBlocks} from '@wordpress/block-editor';
import {__} from "@wordpress/i18n";
import {getBlockTypes} from '@wordpress/blocks';
import {getBorderStyle} from "../../utils/get-border-style";
import {createAttrGetter} from "../../utils/global-settings";

export default function AccordionBody(props) {
    const {isEditor, attributes} = props;
    const InnerBlocks = isEditor ? DefaultInnerBlocks : DefaultInnerBlocks.Content;
    const EXCLUDED_BLOCKS = ["aab/accordion-item"];
    const ALLOWED_BLOCKS = getBlockTypes().map(block => block.name).filter(blockName => !EXCLUDED_BLOCKS.includes(blockName))

    const {
        uniqueId,
        QaStyle,
        makeActive,
        feedbackShow,
        iconPosition,
        feedbacLabel,
        yesBtn,
        noBtn,
        counterShow,
        accessibilityOn
    } = attributes;

    const aab_premium = aagb_local_object.licensing;
    const getAttrDeep = createAttrGetter(attributes);
    const paddings = getAttrDeep('paddings') ?? {};
    const bodyBg = getAttrDeep('bodyBg') ?? '';
    const bodyBorder = getAttrDeep('bodyBorder') ?? null;
    const borderRadius = getAttrDeep('borderRadius') ?? null;
    const bodyBorderStyle = getBorderStyle(bodyBorder);

    return (
        <>
            <div
                className={`aab__accordion_body ${makeActive ? `aab__accordion_body--show` : ``} ${makeActive ? `active__accordion_${uniqueId}` : ''}`}
                role="region"
                style={
                    {
                        ...(bodyBg && {backgroundColor: bodyBg}),
                        display: makeActive ? 'block' : 'none',
                        ...(borderRadius && { borderRadius: `0 0 ${borderRadius}px ${borderRadius}px`}),
                        ...bodyBorderStyle,
                        ...(!QaStyle ? {
                                ...(Object.values(paddings).some(val => val) && {
                                        padding: `${paddings.top} ${paddings.left} ${paddings.bottom} ${paddings.right}`
                                    }
                                ),
                            } : QaStyle && iconPosition === "aab_left_icon" ? {
                                borderTop: 'none !important',
                                paddingTop: `0`,
                                ...(paddings.bottom && {paddingBottom: paddings.bottom}),
                                ...(paddings.right && {paddingRight: paddings.right}),
                                paddingLeft: `calc(${paddings.left ?? '15px'} + 140px)`
                            } : {
                                borderTop: 'none !important',
                                paddingTop: `0`,
                                ...(paddings.bottom && {paddingBottom: paddings.bottom}),
                                ...(paddings.right && {paddingRight: paddings.right}),
                                paddingLeft: `calc(${paddings.left ?? '15px'} + 90px)`
                            }
                        ),
                    }
                }
            >
                <div className='aab__accordion_component'>
                    <InnerBlocks
                        allowedBlocks={ALLOWED_BLOCKS}
                        template={[
                            [
                                'core/paragraph',
                                {
                                    content: 'Accordion Content',
                                },
                            ],
                        ]}
                    />

                    {
                        feedbackShow && aab_premium && (
                            <span className={`feedback-btn-wrap ${accessibilityOn && `accessible-feedback`}`}
                                  data-id={uniqueId}>
                {feedbacLabel && <span>{feedbacLabel}</span>}

                                {
                                    yesBtn && (
                                        <button
                                            className="feedback-btn"
                                            data-value="yes"
                                            data-id={uniqueId}
                                            tabIndex={accessibilityOn ? 0 : -1}
                                        >
                                            {yesBtn}
                                            {counterShow && (
                                                <span className="count">--</span>
                                            )}
                                        </button>
                                    )
                                }

                                {
                                    noBtn && (
                                        <button
                                            className="feedback-btn"
                                            data-value="no"
                                            data-id={uniqueId}
                                            tabIndex={accessibilityOn ? 0 : -1}
                                        >
                                            {noBtn}
                                            {counterShow && (
                                                <span className="count">--</span>
                                            )}
                                        </button>
                                    )
                                }
              </span>
                        )
                    }
                </div>
            </div>
        </>
    );
};