import { PanelBody, ToggleControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import React from "react";
import {createAttrGetter} from "../../../utils/global-settings";

export default function FaqSchemaPanel({ attributes, setAttributes }) {
  const getAttrDeep = createAttrGetter(attributes);
  const faqSchema = getAttrDeep('faqSchema') ?? false;
  return (
    <PanelBody
      initialOpen={false}
      title={__('FAQ Schema', 'advanced-accordion-block')}
    >
      <ToggleControl
        label={__(
          'Make it enable',
          'advanced-accordion-block'
        )}
        checked={faqSchema}
        onChange={(faqSchema) => setAttributes({ faqSchema })}
      />
    </PanelBody>
  );
}