import {registerBlockType} from '@wordpress/blocks';
import './style.scss';
import metadata from './block.json';

/**
 * Internal dependencies
*/
import Edit from './edit';
import Save from './save';
import saveDep from './deprecated';

import AccordionBlockIcon from '../components/icons/block-icons/accordion';

/**
 * Block Registration
 */
registerBlockType(metadata, {
    icon: {
        src: AccordionBlockIcon,
        foreground: '#3E58E1',
    },
    edit: Edit,
    save: Save,
    deprecated: [
        {
          "attributes": {
            "style": {
              "type": "object",
              "default": {
                "border": {
                  "color": "#CFCABE",
                  "radius": "3px",
                  "style": "dash",
                  "width": "1px"
                }
              }
            },
            "uniqueId": {
              "type": "string"
            },
            "border": {
              "type": "object",
              "default": {
                "color": "#bcb6b638",
                "style": "solid",
                "width": "1px"
              }
            },
            "paddings": {
              "type": "object",
              "default": {
                "top":"",
                "right":"",
                "left":"",
                "bottom":""
              }
            },
            "margins": {
              "type": "object",
              "default": {
                "top":"0px",
                "right":"",
                "left":"",
                "bottom":"15px"
              }
            },
            "borderRadius": {
              "type": "number",
              "default": "15"
            },
            "qIconText": {
              "type": "string",
              "default": "Q"
            },
            "qIconColor": {
              "type": "string",
              "default": "#fff"
            },
            "qIconBg": {
              "type": "string",
              "default": "#505050"
            },
            "aIconText": {
              "type": "string",
              "default": "A"
            },
            "aIconColor": {
              "type": "string",
              "default": "#fff"
            },
            "aIconBg": {
              "type": "string",
              "default": "#f5a623"
            },
            "heading": {
              "type": "string",
              "default": "Accordion Heading"
            },
            "subheading": {
              "type": "string",
              "default": ""
            },
            "subheadingColor": {
              "type": "string"
            },
            "headingTag": {
              "type": "string",
              "default": "h5"
            },
            "headingColor": {
              "type": "string"
            },
            "headerBg": {
              "type": "string",
              "default": "#bcb6b638"
            },
            "headingIconImageUrl": {
              "type": "string",
              "default": ""
            },
            "headingIconAlt": {
              "type": "string",
              "default": ""
            },
            "showHeadingIcon": {
              "type": "boolean",
              "default": false
            },
            "showIcon": {
              "type": "boolean",
              "default": true
            },
            "anchorLinkShow": {
              "type": "boolean",
              "default": false
            },
            "iconClass": {
              "type": "string",
              "default": "plus-alt2"
            },
            "iconPosition": {
              "type": "string",
              "default": "aab_right_icon"
            },
            "iconFontSize": {
              "type": "number",
              "default": 23
            },
            "iconColor": {
              "type": "string"
            },
            "iconBackground": {
              "type": "string"
            },
            "iconBorder": {
              "type": "object",
              "default": {
                "width": "0px",
                "style": "solid",
                "color": "transparent"
              }
            },
            "iconBorderRadius": {
              "type": "string"
            },
            "bodyBg": {
              "type": "string"
            },
            "makeActive": {
              "type": "boolean",
              "default": false
            },
            "id": {
              "type": "string"
            },
            "linkedAccordion": {
              "type": "boolean",
              "default": false
            },
            "link": {
              "type": "string",
              "default": "#"
            },
            "tab": {
              "type": "boolean",
              "default": false
            },
            "disableAccordion": {
              "type": "boolean",
              "default": false
            },
            "feedbackShow": {
              "type": "boolean",
              "default": false
            },
            "feedbacLabel": {
              "type": "string",
              "default": "Was this answer helpful?"
            },
            "yesBtn": {
              "type": "string",
              "default": "Yes"
            },
            "noBtn": {
              "type": "string",
              "default": "No"
            },
            "counterShow": {
              "type": "boolean",
              "default": false
            },
            "incNumber": {
              "type": "number",
              "default": 1
            },
            "anchorPosition": {
              "type": "string",
              "default": "aab_right_link"
            },
            "faqSchema": {
              "type": "boolean",
              "default": false
            },
            "QaStyle": {
              "type": "boolean",
              "default": false
            },
            "customCSS": {
              "type": "string",
              "default": ""
            },
            "headingBorder": {
              "type": "object",
              "default": {
                "color": null,
                "style": "solid",
                "width": "0px"
              }
            },
            "bodyBorder": {
              "type": "object",
              "default": {
                "top": {
                  "color": "#bcb6b638",
                  "style": "solid",
                  "width": "1px"
                }
              }
            },
            "accessibilityOn": {
              "type": "boolean",
              "default": true
            },
            "focusOutlineColor": {
              "type": "string",
              "default": "#C2DBFE"
            },
            "defaultStyles": {
              "type": "object"
            }
          },
            save: saveDep,
        },
    ],
});

