import React from "react";
import {useBlockProps, RichText, InnerBlocks} from '@wordpress/block-editor';
import {__} from "@wordpress/i18n";
import disableQAButton from "../utils/disable-qa-button";
import {getBorderStyle} from "../utils/get-border-style";

const saveDep = (props) => {
    const {attributes} = props;
    const {
        uniqueId,
        disableAccordion,
        makeActive,
        border,
        margins,
        borderRadius,
        anchorLinkShow,
        headingColor,
        customCSS,
        accessibilityOn,
        focusOutlineColor,
        paddings,
        qIconText,
        qIconColor,
        qIconBg,
        aIconText,
        aIconColor,
        aIconBg,
        heading,
        subheading,
        subheadingColor,
        headingTag,
        anchorPosition,
        showIcon,
        iconClass,
        iconPosition,
        iconColor,
        iconFontSize,
        iconBackground,
        headerBg,
        headingIconImageUrl,
        headingIconAlt,
        showHeadingIcon,
        iconBorder,
        iconBorderRadius,
        QaStyle,
        headingBorder,
        feedbackShow,
        bodyBg,
        feedbacLabel,
        yesBtn,
        noBtn,
        counterShow,
        bodyBorder
    } = attributes;

    const aab_premium = aagb_local_object.licensing;
    const headingBorderStyle = getBorderStyle(headingBorder);
    const calculatedHeaderBg = QaStyle && (headerBg.toLowerCase() === "#bcb6b638") ? "transparent" : headerBg;
    let subheadingPlaceholder = "";
    if (!aab_premium) disableQAButton();
    const borderStyle = getBorderStyle(border);
    const bodyBorderStyle = getBorderStyle(bodyBorder);
    return (<>
        <div className="custom-css-block">
            <style>
                {`#aab_accordion_${uniqueId} { ${customCSS} }`}
                {anchorLinkShow && headingColor && aab_premium && `
                            #aab_accordion_${uniqueId} .aab__accordion_heading .anchorjs-link { 
                              color: ${headingColor};
                            }
                          `}
                {accessibilityOn && focusOutlineColor !== "#C2DBFE" && `#aab_accordion_${uniqueId}:focus-visible {
                              outline: 2px solid ${focusOutlineColor};
                            }
                          `}
            </style>
        </div>
        <div
            {...useBlockProps.save({
                className: `aab__accordion_container ${disableAccordion ? 'aab__accordion_disabled' : ''} ${accessibilityOn && 'accessibilityOn'} ${makeActive ? `active__accordion_container_${uniqueId} ` : ''}`
            })}
            style={{
                marginTop: margins.top ?? '0px',
                marginBottom: margins.bottom ?? '0px', ...(borderRadius && {borderRadius: borderRadius + 'px'}), ...borderStyle, // Spread shorthand or individual border values
            }}
            id={`aab_accordion_${uniqueId}`}
            role="button"
            aria-expanded={makeActive}
            tabIndex={accessibilityOn ? 0 : -1}
        >
            <div
                className={`aab__accordion_head ${iconPosition} ${makeActive ? 'active' : ''} `}
                style={{
                    backgroundColor: headerBg ? calculatedHeaderBg : undefined, ...(Object.values(paddings).some(val => val) && {
                        padding: `${paddings.top} ${paddings.left} ${paddings.bottom} ${paddings.right}`
                    }), ...headingBorderStyle
                }}
            >
                <div
                    className={`aab__accordion_heading ${iconPosition} ${anchorPosition}`}
                >
                    {showHeadingIcon && headingIconImageUrl && !QaStyle && (<div className="heading-icon">
                        <img
                            src={headingIconImageUrl}
                            alt={headingIconAlt || __('Heading Icon', 'advanced-accordion-block')}
                        />
                    </div>)}
                    {QaStyle && aab_premium && (<div className="icon-container">
                        <div className="icon-q"
                             style={{
                                 color: qIconColor, backgroundColor: qIconBg
                             }}
                        >{qIconText}</div>
                        <div className="icon-a"
                             style={{
                                 color: aIconColor, backgroundColor: aIconBg,
                             }}>{aIconText}</div>
                    </div>)}
                    <div className="head_content_wrapper">
                        <div className="title_wrapper">
                            <RichText.Content
                                tagName={headingTag}
                                value={heading}
                                className="aab__accordion_title"
                                style={{
                                    margin: 0, color: headingColor,
                                }}
                            />
                            {anchorLinkShow && aab_premium && (
                                <a tabIndex={accessibilityOn ? 0 : -1} className="anchorjs-link" href="#">
                                    <i className="dashicons dashicons-admin-links"></i>
                                </a>)}
                        </div>
                        <RichText.Content
                            className="aab__accordion_subheading"
                            tagName="p"
                            placeholder={subheadingPlaceholder}
                            value={aab_premium ? subheading : ''}
                            onFocus={(e) => {
                                if (!aab_premium) {
                                    e.target.blur(); // Prevent focus if aab_premium is false
                                }
                            }}
                            style={{
                                margin: "5px 0 0 0", color: subheadingColor,
                            }}
                        />
                    </div>
                </div>
                {showIcon && (<div
                    className={`aab__accordion_icon`}
                    style={{
                        ...(iconColor && {color: iconColor}),
                        border: iconBorder ? `${iconBorder.width} ${iconBorder.style} ${iconBorder.color}` : '', ...(iconBorderRadius && {borderRadius: iconBorderRadius}), ...(iconBackground && {backgroundColor: iconBackground})
                    }}
                >
                    <span
                        className={`aab__icon dashicons dashicons-${iconClass}`}
                        style={{
                            fontSize: iconFontSize ? iconFontSize + 'px' : '',
                        }}
                    />
                </div>)}
            </div>
            <div
                className={`aab__accordion_body ${makeActive ? `aab__accordion_body--show` : ``} ${makeActive ? `active__accordion_${uniqueId}` : ''}`}
                role="region"
                style={{
                    ...(bodyBg && {backgroundColor: bodyBg}),
                    display: makeActive ? 'block' : 'none',
                    ...bodyBorderStyle,
                    ...(!QaStyle ? {
                        ...(Object.values(paddings).some(val => val) && {
                            padding: `${paddings.top} ${paddings.left} ${paddings.bottom} ${paddings.right}`
                        }),
                    } : QaStyle && iconPosition === "aab_left_icon" ? {
                        borderTop: 'none !important',
                        paddingTop: `0`, ...(paddings.bottom && {paddingBottom: paddings.bottom}), ...(paddings.right && {paddingRight: paddings.right}),
                        paddingLeft: `calc(${paddings.left ?? '15px'} + 140px)`
                    } : {
                        borderTop: 'none !important',
                        paddingTop: `0`, ...(paddings.bottom && {paddingBottom: paddings.bottom}), ...(paddings.right && {paddingRight: paddings.right}),
                        paddingLeft: `calc(${paddings.left ?? '15px'} + 90px)`
                    }),
                }}
            >
                <div className='aab__accordion_component'>
                    <InnerBlocks.Content
                        template={[['core/paragraph', {
                            content: 'Accordion Content',
                        },],]}
                    />

                    {feedbackShow && aab_premium && (
                        <span className={`feedback-btn-wrap ${accessibilityOn && `accessible-feedback`}`}
                              data-id={uniqueId}>
                                {feedbacLabel && <span>{feedbacLabel}</span>}

                            {yesBtn && (<button
                                className="feedback-btn"
                                data-value="yes"
                                data-id={uniqueId}
                                tabIndex={accessibilityOn ? 0 : -1}
                            >
                                {yesBtn}
                                {counterShow && (<span className="count">--</span>)}
                            </button>)}

                            {noBtn && (<button
                                    className="feedback-btn"
                                    data-value="no"
                                    data-id={uniqueId}
                                    tabIndex={accessibilityOn ? 0 : -1}
                                >
                                    {noBtn}
                                    {counterShow && (<span className="count">--</span>)}
                                </button>
                            )}
                            </span>
                    )}
                </div>
            </div>
        </div>

        {anchorLinkShow === true && aab_premium && (<script>
            {`
								 document.addEventListener("DOMContentLoaded", () => {
                                        var Anchor1 = new AnchorJS();
                                        Anchor1.add('#aab_accordion_${uniqueId} .aab__accordion_heading .title_wrapper');
                                 });
							`}
        </script>)}
    </>);
};

export default saveDep;