import { PanelBody, TextControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import React from "react";
import {createAttrGetter} from "../../../utils/global-settings";

export default function QaIconsPanel({ attributes, setAttributes }) {
  const { QaStyle } = attributes;
  const getAttrDeep = createAttrGetter(attributes);
  const qIconText = getAttrDeep('qIconText') ?? null;
  const aIconText = getAttrDeep('aIconText') ?? null;

  if(!QaStyle) return <></>;

  return (
    <PanelBody
      initialOpen={false}
      title={__('Q/A Icons', 'advanced-accordion-block')}
    >
      <p className="aab__label">
        {__('Q Icon Text', 'advanced-accordion-block')}
      </p>
      <TextControl
        label={__(
          'Set Q Icon Text',
          'advanced-accordion-block'
        )}
        value={qIconText}
        onChange={(qIconText) => setAttributes({ qIconText })}
      />
      <p className="aab__label">
        {__('A Icon Text', 'advanced-accordion-block')}
      </p>
      <TextControl
        label={__(
          'Set A Icon Text',
          'advanced-accordion-block'
        )}
        value={aIconText}
        onChange={(aIconText) => setAttributes({ aIconText })}
      />
    </PanelBody>
  );
}